.story {
  display: flex;
  flex-direction: row;
  margin-bottom: 10px;

  .story__about {
    display: flex;
    flex-direction: column;
    margin-left: 55px;
    @media (max-width: $breakpoint992) {
      margin-left: 0;
      margin-top: 15px;
    }

    .story__title {
      font-style: normal;
      font-weight: 700;
      font-size: 28px;
      line-height: 110%;
      letter-spacing: -0.03em;
      color: $storyText;
      margin-bottom: 20px;
      @media (max-width: $breakpoint992) {
        text-align: left;
      }
    }

    .story__text {
      font-family: "Gilroy";
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 175%;
      color: $storyText;
      opacity: 0.75;
      max-width: 366px;

      span {
        margin-bottom: 20px;
      }
    }
    @media (max-width: $breakpoint992) {
      align-content: center;
      flex-wrap: wrap;
    }
  }

  span {
    display: block;
  }

  .story__video {
    margin-top: -120px;
    z-index: 3;
    img {
      max-width: 800px;
      width: 100%;
      height: auto;
    }
    @media (max-width: $breakpoint992) {
      margin-top: 20px;
    }
  }
  @media (max-width: $breakpoint992) {
    flex-direction: column-reverse;
  }
}

.story__wrapper {
  margin-left: 80px;
  margin-top: 80px;
  margin-bottom: 72px;
  @media (max-width: $breakpoint992) {
    margin: 40px 20px 40px;
  }
}
