@import "./reset.scss";
@import "./styles/_colors";
@import "./styles/_media";

@font-face {
  font-family: "Gilroy";
  src: url("./fonts/Gilroy-SemiBold.woff");
  font-weight: 600;
  font-style: normal;
}

//Fonts
$fontMain: "Gilroy", sans-serif;

.container {
  max-width: ($containerWidth + $containerPadding) + px;
  margin: 0 auto;
  padding: 0 $containerSidePadding + px;
  font-family: $fontMain;
  overflow: hidden;
  @media (max-width: $breakpoint480) {
    padding: 5px;
  }
}

.wrapper {
  height: 100vh;
  width: 100%;
  overflow: hidden;
}

.section__wrapper {
  padding-left: 80px;
  padding-right: 80px;
  @media (max-width: $breakpoint768) {
    padding-left: 40px;
    padding-right: 40px;
  }
  @media (max-width: $breakpoint480) {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.btn {
  padding: 15px 22px;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0.01em;
  display: inline-block;
}

.btn__primary {
  color: #ffffff;
  background-color: $secondary;
  border: 3px solid #2c82fd33;
  border-radius: 4px;
  cursor: pointer;
  &:hover {
    background: $secondaryHover;
    transition: 0.3s hover ease;
  }
}

.btn__secondary {
  color: $secondary;
  border-radius: 4px;
  background: #072750;
  border: 3px solid #2c82fd33;

  &:hover {
    background: $secondaryHover;
    transition: 0.3s hover ease;
    color: #ffffff;
  }
}

.title {
  font-size: 42px;
  line-height: 46px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  color: $storyText;
}

.hidden {
  display: none !important;
}

.responsive__img {
  max-width: 800px;
  width: 100%;
  height: auto;
}

@import "./styles/header/header.scss";
@import "./styles/header/hamburger-nav.scss";
@import "./styles/header/navbar.scss";
@import "./styles/hero/hero.scss";
@import "./styles/story/story.scss";
@import "./styles/accordion/accordion.scss";
@import "./styles/features/features.scss";
@import "./styles/pricing/pricing.scss";
@import "./styles/learn/learn.scss";
@import "./styles/reviews/reviews.scss";
@import "./styles/faq/faq.scss";
@import "./styles/footer/footer.scss";
